import React from "react";
import Columns from 'react-bulma-components/lib/components/columns';
import Matchup from "./Matchup";

class BracketRow extends React.Component {
    render() {
      return (
          <div>
            <Columns>
                <Columns.Column size={1}>
                {this.props.round === 0 &&
                    <Matchup
                        pickWinner={this.props.pickWinner}
                        unPickWinner={this.props.unPickWinner}
                        round={this.props.round}
                        division={this.props.divisions[0]}
                        matchId={this.props.matchId}
                        key={this.props.matchId}
                        teams={this.props.teams}
                        bracket={this.props.bracket}
                    >
                    </Matchup>
                }
                </Columns.Column>
                <Columns.Column size={1}>
                {this.props.round === 1 &&

                    <Matchup
                        pickWinner={this.props.pickWinner}
                        unPickWinner={this.props.unPickWinner}
                        round={this.props.round}
                        division={this.props.divisions[0]}
                        matchId={this.props.matchId}
                        key={this.props.matchId}
                        teams={this.props.teams}
                        bracket={this.props.bracket}
                    ></Matchup>
                }
                </Columns.Column>
                <Columns.Column size={1}>
                {this.props.round === 2 &&

                    <Matchup
                        pickWinner={this.props.pickWinner}
                        unPickWinner={this.props.unPickWinner}
                        round={this.props.round}
                        division={this.props.divisions[0]}
                        matchId={this.props.matchId}
                        key={this.props.matchId}
                        teams={this.props.teams}
                        bracket={this.props.bracket}
                    ></Matchup>
                }
                </Columns.Column>
                <Columns.Column size={1}>
                {this.props.round === 3 &&

                    <Matchup
                        pickWinner={this.props.pickWinner}
                        unPickWinner={this.props.unPickWinner}
                        round={this.props.round}
                        division={this.props.divisions[0]}
                        matchId={this.props.matchId}
                        key={this.props.matchId}
                        teams={this.props.teams}
                        bracket={this.props.bracket}
                    ></Matchup>
                }
                </Columns.Column>
                <Columns.Column size={1}>
                {this.props.round === 4 &&

                    <Matchup
                        pickWinner={this.props.pickWinner}
                        unPickWinner={this.props.unPickWinner}
                        round={this.props.round}
                        division={this.props.divisions[0]}
                        matchId={this.props.matchId}
                        key={this.props.matchId}
                        teams={this.props.teams}
                        bracket={this.props.bracket}
                    ></Matchup>
                }
                </Columns.Column>
                <Columns.Column size={1}>
                {this.props.round === 5 &&

                    <Matchup
                        pickWinner={this.props.pickWinner}
                        unPickWinner={this.props.unPickWinner}
                        round={this.props.round}
                        division={this.props.divisions[0]}
                        matchId={this.props.matchId}
                        key={this.props.matchId}
                        teams={this.props.teams}
                        bracket={this.props.bracket}
                    ></Matchup>
                }
                </Columns.Column>
                <Columns.Column size={1}>
                {this.props.round === 4 &&

                    <Matchup
                        pickWinner={this.props.pickWinner}
                        unPickWinner={this.props.unPickWinner}
                        round={this.props.round}
                        division={this.props.divisions[1]}
                        matchId={this.props.matchId}
                        key={this.props.matchId}
                        teams={this.props.teams}
                        bracket={this.props.bracket}
                    ></Matchup>
                }
                </Columns.Column>
                <Columns.Column size={1}>
                {this.props.round === 3 &&

                    <Matchup
                        pickWinner={this.props.pickWinner}
                        unPickWinner={this.props.unPickWinner}
                        round={this.props.round}
                        division={this.props.divisions[1]}
                        matchId={this.props.matchId}
                        key={this.props.matchId}
                        teams={this.props.teams}
                        bracket={this.props.bracket}
                    ></Matchup>
                }
                </Columns.Column>
                <Columns.Column size={1}>
                {this.props.round === 2 &&

                    <Matchup
                        pickWinner={this.props.pickWinner}
                        unPickWinner={this.props.unPickWinner}
                        round={this.props.round}
                        division={this.props.divisions[1]}
                        matchId={this.props.matchId}
                        key={this.props.matchId}
                        teams={this.props.teams}
                        bracket={this.props.bracket}
                    ></Matchup>
                }
                </Columns.Column>

                <Columns.Column size={1}>
                {this.props.round === 1 &&

                    <Matchup
                        pickWinner={this.props.pickWinner}
                        unPickWinner={this.props.unPickWinner}
                        round={this.props.round}
                        division={this.props.divisions[1]}
                        matchId={this.props.matchId}
                        key={this.props.matchId}
                        teams={this.props.teams}
                        bracket={this.props.bracket}
                    ></Matchup>
                }
                </Columns.Column>
                <Columns.Column size={1}>
                {this.props.round === 0 &&
                    <Matchup
                        pickWinner={this.props.pickWinner}
                        unPickWinner={this.props.unPickWinner}
                        round={0}
                        division={this.props.divisions[1]}
                        matchId={this.props.matchId}
                        key={this.props.matchId}
                        teams={this.props.teams}
                        bracket={this.props.bracket}
                    >
                    </Matchup>
                }
                </Columns.Column>
            </Columns>
          </div>
        );
    }
}


export default BracketRow;