import React from "react";
import styled from "styled-components";
import Team from "./Team";

const Wrapper = styled.section`
  width: 100%;
`;

class Matchup extends React.Component {
  // handleDetermineWinner = (children, winner) => {
  //   const { team: teamA } = children[0].props;
  //   const { team: teamB } = children[1].props;

  //   this.props.determineWinner({
  //   //   teamA,
  //   //   teamB,
  //     round: this.props.round,
  //     division: this.props.division,
  //     matchId: this.props.matchId,
  //     winner
  //   });
  // };






  render() {
    const teamA = this.props.teams[this.props.bracket[this.props.division][this.props.round][this.props.matchId]["teamA"]]
    const teamB = this.props.teams[this.props.bracket[this.props.division][this.props.round][this.props.matchId]["teamB"]]

    var teamAStatus = "tied";
    var teamBStatus = "tied";

    if (teamA && teamB && teamA.currentRoundScore > teamB.currentRoundScore) {
      if (teamA.currentRoundScore >= 0) {
        teamAStatus = "darkGreen";
      } else {
        teamAStatus = "lightRed";
      }
      if (teamB.currentRoundScore >= 0) {
        teamBStatus = "lightGreen";
      } else {
        teamBStatus = "darkRed";
      }
    }

    if (teamA && teamB && teamA.currentRoundScore < teamB.currentRoundScore) {
      if (teamA.currentRoundScore >= 0) {
        teamAStatus = "lightGreen";
      } else {
        teamAStatus = "darkRed";
      }
      if (teamB.currentRoundScore >= 0) {
        teamBStatus = "darkGreen";
      } else {
        teamBStatus = "lightRed";
      }
    }
    // const { team: teamA, winner: winnerA } = this.props.teams[this.props.bracket[this.props.division][this.props.round][this.props.matchId]["teamA"]];
    // const { team: teamB, winner: winnerB } = this.props.teams[this.props.bracket[this.props.division][this.props.round][this.props.matchId]["teamB"]];


    // const playGame =
    //   teamA && teamB && !winnerA && !winnerB ? (
    //     <button onClick={() => this.handleDetermineWinner(this.props.children)}>
    //       Play
    //     </button>
    //   ) : (
    //     ""
    //   );

    return (
      <Wrapper>

        <Team
            pickWinner={this.props.pickWinner}
            unPickWinner={this.props.unPickWinner}
            team={teamA}
            status={teamAStatus}
            round={this.props.round}
            division={this.props.division}
            matchId={this.props.matchId}
            winner={false}
            handleSeedChange={this.props.handleSeedChange}
            />
        <Team
            pickWinner={this.props.pickWinner}
            unPickWinner={this.props.unPickWinner}
            team={teamB}
            status={teamBStatus}
            round={this.props.round}
            division={this.props.division}
            matchId={this.props.matchId}
            winner={false}
            handleSeedChange={this.props.handleSeedChange}
            />
      </Wrapper>
    );
  }
}

export default Matchup;